.search-form {
    display: flex;
    padding: $m-size 0;
    font-size: $m-size;
}

.search-form__input {
    border: none;
    background: $off-white;
    color: $black;
    padding: $sm-size;
    flex-grow: 1;
    -webkit-appearance: none;
    border-radius: 0;
    -webkit-border-radius: 0;
}

.search-form__button {
    background: #f79817;
    color: #fff;
    border: none;
    pointer-events: none;
}